html, body {
    height: 100%;
}

.App {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

h2 {
  font-size: 20px;
}

h3 {
  font-size: 16px;
}

ul {
	list-style-type: none;
	margin: 0;
	padding: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
	-ms-flex-direction: row;
	    flex-direction: row;
	border-bottom: 2px solid black;
}

li {
	font-size: 20px;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	padding: 0;
}

.Link {
	text-decoration: none;
	color: black;
	display: block;
}

.Link:hover {
	background-color: #FAFAFF;
}


